<template>
  <div>
    <el-row :gutter="30">
      <el-form 
        :model="entitySearchForm" 
        :label-position="'left'" 
        label-width="150px" 
        size="mini">
        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-form-item label="Long name">
            <el-input v-model="long_name" />
          </el-form-item>
          <el-form-item label="System ID">
            <el-input v-model="T24_system_id" />
          </el-form-item>
          <el-form-item label="Legal ID">
            <el-input v-model="legal_id" />
          </el-form-item>
        </el-col>
        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-form-item label="Alias">
            <el-input v-model="short_name" />
          </el-form-item>
          <el-form-item label="Tax code">
            <el-input v-model="tax_code" />
          </el-form-item>
          <el-form-item label="Registration number">
            <el-input v-model="registration_number" /> 
          </el-form-item>
        </el-col>
      </el-form>
    </el-row>
    <el-button 
      :disabled="$store.state.RMC.entitySearchLoading"
      type="primary" 
      size="mini" 
      style="margin-left: 37px"
      icon="el-icon-search" 
      @click="$store.dispatch('RMC/searchEntity')">
      Search
    </el-button>
  </div>
</template>
<script>
import { mapFields } from 'vuex-map-fields'

export default {
  name: 'RMCSearchCriteria',

  computed: {
    ...mapFields('RMC', [
      'entitySearchForm',
      'entitySearchForm.long_name',
      'entitySearchForm.short_name',
      'entitySearchForm.T24_system_id',
      'entitySearchForm.legal_id',
      'entitySearchForm.tax_code',
      'entitySearchForm.registration_number'
    ])
  }
}
</script>
<style lang="css" scoped>
.el-row {
  margin-left: 22px !important;
  margin-right: 30px !important;
  &:last-child {
    margin-bottom: 0;
  }
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
>>>.el-form-item__label {
  font-weight: 500;
}
>>>.el-form-item--small.el-form-item {
  margin-bottom: 0;
}
>>>.el-select {
  width: 100%;
}
</style>



