<template>
  <div>
    <el-button-group class="mb-2">
      <el-button 
        :disabled="retrieveRequestLoading"
        size="mini" 
        icon="icon icon-plus" 
        plain 
        @click="TOGGLE_NEW_REQUEST_DIALOG" />
      <el-button 
        :disabled="retrieveRequestLoading"
        size="mini" 
        icon="icon icon-spinner11" 
        plain 
        @click="retrieveRequest" />
    </el-button-group>
    <el-table
      :data="requests.items"
      v-loading="retrieveRequestLoading"
      element-loading-text="Loading..."
      border
      size="small"
      style="width: 100%">
      <el-table-column type="index" width="35" align="center" />
      <el-table-column 
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :prop="c.prop"
        :sortable="c.sortable">
      </el-table-column>
      <el-table-column width="100" fixed="right" label="Operations">
        <template slot-scope="scope">
          <el-button 
            type="text" 
            class="info--text" 
            size="small"
            @click.native.prevent="view(scope.row)">
            View
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      :disabled="retrieveRequestLoading"
      :total="requests.total"
      :page-sizes="[20, 50, 100]"
      :page-size="params.page_size"
      :current-page="params.page_number"
      class="mt-2 right"
      layout="total, sizes, prev, pager, next, jumper"
      background
      @current-change="currentPageChange"
      @size-change="sizeChange">
    </el-pagination>
    <NewRequestDialog />
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import NewRequestDialog from '@/components/RMC/Request/NewRequestDialog'

export default {
  name: 'RequestTable',

  components: {
    NewRequestDialog
  },

  created() {
    this.retrieveRequest()
  },

  computed: {
    ...mapGetters('RMC', ['retrieveRequestLoading', 'requests'])
  },

  data: () => ({
    params: {
      page_size: 20,
      page_number: 1
    },
    columnDefs: [
      { label: 'Request ID', prop: 'id', sortable: true },
      { label: 'Customer name', prop: 'project', sortable: true },
      { label: 'Customer ID', prop: 'customer_id', sortable: true },
      { label: 'Legal ID', prop: 'legal_id', sortable: true },
      { label: 'Type', prop: 'type', sortable: true },
      { label: 'Maked by', prop: 'request_by', sortable: true },
      { label: 'Maked at', prop: 'request_at', sortable: true },
      { label: 'Checked by', prop: 'approve_by', sortable: true },
      { label: 'Checked at', prop: 'approve_at', sortable: true },
      { label: 'Status', prop: 'status', sortable: true }
    ]
  }),

  methods: {
    ...mapMutations('RMC', ['TOGGLE_NEW_REQUEST_DIALOG']),

    retrieveRequest() {
      this.$store.dispatch('RMC/retrieveRequest', { params: this.params })
    },

    currentPageChange(val) {
      this.params.page_number = val
      this.retrieveRequest()
    },

    sizeChange(val) {
      this.params.page_size = val
      this.retrieveRequest()
    }
  }
}
</script>



