<template>
  <el-dialog 
    :visible="dialogNewEntityVisible"
    :before-close="TOGGLE_NEW_ENTITY_DIALOG"
    :width="'35%'"
    title="Create new entity">
    <el-form 
      :model="form" 
      :rules="rules" 
      ref="newEntityForm" 
      label-width="150px" 
      size="mini" 
      label-position="left">
      <el-form-item label="Long name" prop="long_name">
        <el-input v-model="form.long_name" />
      </el-form-item>

      <el-form-item label="Alias" prop="alias">
        <el-input v-model="form.alias" />
      </el-form-item>

      <el-form-item label="Tax code" prop="tax_code">
        <el-input v-model="form.tax_code" />
      </el-form-item>

      <el-form-item label="System ID" prop="system_id">
        <el-input v-model="form.system_id" />
      </el-form-item>

      <el-form-item label="Legal ID" prop="legal_id">
        <el-input v-model="form.legal_id" />
      </el-form-item>

      <el-form-item label="Registration number" prop="registration_number">
        <el-input v-model="form.registration_number" />
      </el-form-item>

      <el-form-item label="Entity type" prop="type">
        <span>{{ type ? type.value : '' }}</span>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button size="small" type="primary" @click="submit('newEntityForm', false)">Save</el-button>
      <el-button size="small" type="primary" @click="submit('newEntityForm', true)">Save & Add Another</el-button>
      <el-button size="small" @click="TOGGLE_NEW_ENTITY_DIALOG">Cancel</el-button>
    </span>
  </el-dialog>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
  props: {
    type: Object,
    required: true
  },

  computed: {
    ...mapGetters('RMC', ['dialogNewEntityVisible'])
  },

  data: () => ({
    form: {
      long_name: null,
      alias: null,
      system_id: null,
      legal_id: null,
      tax_code: null,
      registration_number: null
    },

    rules: {
      long_name: [{ required: true, message: 'Please input', trigger: 'blur' }],
      alias: [{ required: true, message: 'Please input', trigger: 'blur' }],
      tax_code: [{ required: true, message: 'Please input', trigger: 'blur' }]
    },

    loading: false,
    options: []
  }),

  methods: {
    ...mapMutations('RMC', ['TOGGLE_NEW_ENTITY_DIALOG']),
    // ...mapActions('RMC', ['addOtherFI']),

    submit(formName, keep) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.form.type = this.type.id
          console.log(this.form)
          // this.addOtherFI(JSON.parse(JSON.stringify(this.form)), keep)
          if (!keep) this.TOGGLE_NEW_ENTITY_DIALOG()
        }
        return false
      })
    }
  }
}
</script>
<style lang="css" scoped>
>>>.el-form-item__label {
  font-weight: 500;
}
>>>.el-form-item--small.el-form-item {
  margin-bottom: 0;
}
>>>.el-select {
  width: 100%;
}
>>>.el-dialog__body {
  padding: 20px 30px;
}
</style>



