<template>
  <div>
    <PanelGroup />
    <RequestTable />
  </div>
</template>

<script>
import CountTo from 'vue-count-to'
import permission from '@/directive/permission/index.js'
import PanelGroup from '@/components/RMC/Request/PanelGroup'
import RequestTable from '@/components/RMC/Request/RequestTable'

export default {
  name: 'RMCRequests',

  components: {
    CountTo,
    PanelGroup,
    RequestTable
  },

  directives: { permission }
}
</script>



