<template>
  <div class="mx-3">
    <div class="mb-2">
      <el-dropdown
        v-if="allowCreate"
        v-permission="['superuser', 'RMCAdmin']"
        trigger="click" 
        placement="bottom-start"
        @command="createEntity">
        <el-tooltip class="item" effect="dark" content="Create entity" placement="top-start">
          <el-button :disabled="entitySearchLoading || !entity_types.length" size="mini" icon="icon icon-plus" plain />
        </el-tooltip>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item 
            v-for="e in entity_types"
            :key="e.id"
            :command="e">
            {{ e.value }}
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-popover
        v-if="allowRequest"
        v-model="popoverVisible"
        placement="right"
        trigger="click">
        <el-cascader-panel 
          v-model="request_type"
          :options="options" 
          clearable 
          @change="onChangeCascader" />
        <el-button 
          slot="reference" 
          :disabled="entitySearchLoading || !currentEntity" 
          size="mini" 
          icon="icon icon-plus"
          plain />
      </el-popover>
      <!-- <el-dropdown 
        v-if="allowRequest"
        trigger="click" 
        placement="bottom-start"
        @command="createRequest">
        <el-tooltip class="item" effect="dark" content="Add to request" placement="top-start">
          <el-button :disabled="entitySearchLoading || !currentEntity" size="mini" icon="icon icon-plus" plain />
        </el-tooltip>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item 
            v-for="r in request_types"
            :key="r.id"
            :command="r">
            {{ r.value }}
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown> -->
      <el-tooltip class="item" effect="dark" content="View" placement="top-start">
        <el-button 
          v-if="allowView"
          v-permission="['superuser', 'RMCAdmin']"
          size="mini" 
          icon="icon icon-folder-open" 
          plain 
          :disabled="entitySearchLoading || !currentEntity" 
          @click="view" />
      </el-tooltip>
    </div>
    <el-table 
      :data="entitySearchResult.items" 
      v-loading="entitySearchLoading" 
      element-loading-text="Loading..."
      size="mini"
      border 
      highlight-current-row
      @current-change="handleCurrentChange">
      <el-table-column type="index" width="35" align="center" :index="generateIndex" />
      <el-table-column 
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :prop="c.prop"
        :sortable="c.sortable">
      </el-table-column>
    </el-table>
    <el-pagination
      v-if="entitySearchResult.total"
      :disabled="entitySearchLoading"
      :total="entitySearchResult.total"
      :page-sizes="[20, 50, 100]"
      :page-size="page_size"
      :current-page="page_number"
      class="mt-2"
      layout="total, sizes, prev, pager, next, jumper"
      background
      @current-change="currentPageChange"
      @size-change="sizeChange">
    </el-pagination>
    <NewEntityDialog :type="entity_type" />
  </div>
</template>

<script>
import { mapFields } from 'vuex-map-fields'
import { mapMutations, mapGetters } from 'vuex'
import NewEntityDialog from '@/components/RMC/Entity/Create/NewEntityDialog'
import permission from '@/directive/permission/index'

export default {
  name: 'RMCSearchResult',

  components: {
    NewEntityDialog
  },

  directives: { permission },

  props: {
    allowCreate: {
      type: Boolean,
      default: false
    },

    allowView: {
      type: Boolean,
      default: false
    },

    allowRequest: {
      type: Boolean,
      default: false
    }
  },

  mounted() {
    if (this.allowCreate) {
      this.$services.RMCReference.get('entity_types')
        .then(res => this.entity_types = res.data)
    }
  },

  computed: {
    ...mapGetters('RMC', [
      'entitySearchLoading', 
      'entitySearchResult'
    ]),

    ...mapFields('RMC', [
      'entitySearchForm.page_size',
      'entitySearchForm.page_number'
    ]),

    generateIndex() {
      if (this.page_number === 1) return this.page_number
      else return (this.page_number - 1) * this.page_size + 1
    }
  },

  data: () => ({
    currentEntity: null,
    entity_type: null,
    entity_types: [],
    columnDefs: [
      { label: 'Long name', prop: 'long_name', sortable: true, width: 300 },
      { label: 'Alias', prop: 'short_name', sortable: true },
      { label: 'Entity type', prop: 'entity_type', sortable: true },
      { label: 'Business type', prop: 'business_type', sortable: true },
      { label: 'T24 system ID', prop: 'T24_system_id', sortable: true },
      { label: 'Legal ID', prop: 'legal_id', sortable: true },
      { label: 'Tax code', prop: 'tax_code', sortable: true },
      { label: 'Registration number', prop: 'registration_number', sortable: true },
      { label: 'Stock code', prop: 'stock_code', sortable: true }
    ],
    request_types: [
      { value: 'Whole Group', id: 'whole_group' },
      { value: 'Only Childrens', id: 'only_childrens' },
      { value: 'Only Parents', id: 'only_parents' },
      { value: 'Only Stakeholder', id: 'stakeholder' }
    ],
    popoverVisible: false,
    request_type: null,
    options: [
      {
        value: 'guide',
        label: 'Request type',
        disabled: true
      }, 
      { 
        label: 'Whole Group', 
        value: 'whole_group' 
      },
      { 
        label: 'Only Childrens', 
        value: 'only_childrens' 
      },
      { 
        label: 'Only Parents', 
        value: 'only_parents' 
      },
      { 
        label: 'Only Stakeholder', 
        value: 'stakeholder' 
      }
    ]
  }),

  methods: {
    ...mapMutations('RMC', [
      'TOGGLE_NEW_ENTITY_DIALOG', 
      'ENTITY_SEARCH_LOADING'
    ]),

    createEntity(entity_type) {
      this.entity_type = entity_type
      this.TOGGLE_NEW_ENTITY_DIALOG()
    },

    createRequest() {
      this.$store.dispatch('RMC/addRequest', { 
        entity_id: this.currentEntity.id,
        type: this.request_type
      })
        .then(res => console.log(res))
    },

    view() {
      if (this.currentEntity) {
        this.$router.push({ path: `/rmc/entity/${this.currentEntity.id}` })
      }
    },

    handleCurrentChange(val) {
      this.currentEntity = val
    },

    currentPageChange(val) {
      this.page_number = val
      this.$store.dispatch('RMC/searchEntity')
    },

    sizeChange(val) {
      this.page_size = val
      this.$store.dispatch('RMC/searchEntity')
    },

    onChangeCascader() {
      this.$confirm('Are you sure?', 'Confirm', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning'
      }).then(() => {
        console.log(this.request_type)
        this.popoverVisible = false
      })
    }
  }
}
</script>
<style lang="css">
  .el-popover {
    padding: 0 !important;
  }
</style>



