<template>
  <div>
    <el-dialog
      :visible.sync="dialogNewRequestVisible"
      :before-close="TOGGLE_NEW_REQUEST_DIALOG"
      title="Add new request"
      width="90%">
      <el-collapse v-model="activePanels">
        <el-collapse-item name="criteria">
          <template slot="title">
            <i class="el-icon-search" />
            <span class="primary--text">&nbsp; Search Criteria</span>
          </template>
          <SearchCriteria class="mt-1" @search="search" />
        </el-collapse-item>

        <el-collapse-item name="result">
          <template slot="title">
            <i class="el-icon-check" />
            <span class="primary--text">&nbsp; Search Result</span>
          </template>
          <SearchResult :entities="entities" class="mb-3" allow-request />
        </el-collapse-item>
      </el-collapse>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import SearchCriteria from '@/components/RMC/Entity/Search/Criteria'
import SearchResult from '@/components/RMC/Entity/Search/Result'

export default {
  name: 'NewRequestDialog',

  components: {
    SearchCriteria,
    SearchResult
  },

  computed: {
    ...mapGetters('RMC', ['dialogNewRequestVisible'])
  },

  data: () => ({
    activePanels: ['criteria', 'result'],
    entities: []
  }),

  methods: {
    ...mapMutations('RMC', ['TOGGLE_NEW_REQUEST_DIALOG']),

    search(form) {
      Object.keys(form).forEach((key) => (!form[key]) && delete form[key])
      if (Object.keys(form).length !== 0) {
        this.$store.dispatch('RMC/searchEntity', form)
          .then(entities => this.entities = entities)
      }
    }
  }
}
</script>
<style scoped>
  >>>.el-dialog__body {
    padding: 0;
  }
  >>>.el-collapse-item__wrap {
    border-bottom: none;
  }
</style>



